.Home {
    padding: 1em;
    align-content: center;
    width: 90vw;
    height: 50%;
    display: flex;
    justify-content: space-between;
    font-family: 'Nanum Myeongjo', serif;
  
    .volume-controllers {
      display: flex;
      width: 30%; // Keeping the width unchanged
      flex-direction: column;
      margin-top: 0em; // Adjusted margin
      gap: 0px; // Add a gap between controllers to reduce height
    }
  
    .volume-controllers div {
      margin: 0px;
      padding: 0px;
    }
  
    .image-div img {
      height: 80%; // Keeping the image size unchanged
      width: 80%;
    }

  }
  