.sliderTitle {
    font-weight: 400;
    width: 200px;
    font-size: 15px;
    display: flex;
    margin-bottom: 2px;

}

.volume-controllers {
    display: flex;
    flex-direction: row;
    align-items: center;

    div {
        width:90%;
    }

    @media screen and (max-width: 768px) {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .Slider {
        color:black;
        size:"small";
        cursor:pointer;
        border:none;
        background:transparent;

        width: 280px;
    }

    .divAudio{
                width:100%;
                display: flex;
                justify-content: space-between;

    }


}
